import React from "react";
import styles from "./Header.module.css";
import { TopNav, SlashedImage, BottomCurve } from "../../components";
import { Link } from "react-scroll";
import { useQuery } from "@apollo/client";
import { GET_CONTENT } from "../../data";

const Header = (props) => {
  const { loading, error, data } = useQuery(GET_CONTENT);
  
  if (loading) return <p></p>;
  if (error) return <p>Error :(</p>;
  return (
    <header
      className={`${styles.headerWrapper} from-steel-blue to-violet font-Inter`}
    >
      <TopNav />
      <div
        className={`${styles.headerContentWrapper} justify-between mx-4 sm:flex-col xl:flex-row`}
      >
        <div
          className={`${styles.headerLeftContent}text-left xl:max-w-2xl mt-8 space-y-10 lg:ml-10`}
        >
          <h1
            className={`${styles.headerLeftText} text-white text-Tmd md:text-Txl`}
            dangerouslySetInnerHTML={{ __html: data.header[0].heading }}
          ></h1>
          <h2
            className="text-white text-Tsm md:text-Tmd"
            dangerouslySetInnerHTML={{ __html: data.header[0].description_1 }}
          ></h2>
          <Link
            to="services"
            spy={true}
            smooth={true}
            offset={90}
            duration={500}
            className="text-white text-Tmd font-bold"
          >
            <button className="py-4 my-4 ml-2 text-Tsm xl:text-Tmd hover:underline">
              {data.header[0].button_text}
            </button>
          </Link>
        </div>
        <div className={`${styles.headerRightContent} hidden xl:block`}>
          <div
            className={`${styles.image} bg-cover relative h-80 w-25rem ml-auto mr-0`}
          >
            <SlashedImage />
          </div>
          <div className="text-white text-right">
            <h2
              className="text-4xl py-2 my-2"
              dangerouslySetInnerHTML={{ __html: data.header[0].sub_heading }}
            ></h2>
            <p
              className=" text-xl py-2 mx-2 px-4"
              dangerouslySetInnerHTML={{ __html: data.header[0].description_2 }}
            ></p>
          </div>
        </div>
      </div>

      {/* Bottom Curve Wave  */}
      <div className={`${styles.headerBottomCurve} hidden lg:block`}>
        <BottomCurve />
      </div>
    </header>
  );
};

export default Header;
