import React from "react";
import "./About.module.css";
import { startup1, startup2 } from "../../resources/images";
import { useQuery } from "@apollo/client";
import { GET_CONTENT } from "../../data";

function About() {
  const { loading, error, data } = useQuery(GET_CONTENT);
  if (loading) return <p></p>;
  if (error) return <p>Error :(</p>;
  return (
    <section className=" flex flex-col xl:flex-row mx-auto relative lg:p-8 w-full justify-between">
      <div className="w-full xl:w-55 mx-auto py-4 text-steel-black space-y-8 px-8 text-Tsm md:text-Tmd">
        <h1
          className="font-semibold uppercase tracking-wide text-Tmd md:text-Txl pt-4"
          dangerouslySetInnerHTML={{
            __html: data.aboutSection[0].aboutHeading,
          }}
        ></h1>
        <div
          dangerouslySetInnerHTML={{
            __html: data.aboutSection[0].aboutBody,
          }}
        ></div>
      </div>
      <div className="w-full xl:w-45 flex flex-row xl:flex-col">
        <div className="w-45 xl:mx-auto">
          <img
            height="200px"
            width="320px"
            className="mx-auto"
            src={startup1} // image 1
            alt=""
          />
        </div>
        <div className="w-45 xl:mx-auto">
          <img
            height="200px"
            width="250px"
            className=" mx-auto"
            src={startup2} // image 2
            alt=""
          />
        </div>
      </div>
    </section>
  );
}

export default About;
