import React from "react";
import { useMountEffect } from "@react-hookz/web"; // cjs
import "./Hubspot.css";

function HubspotForm({ portalId, formId, region }) {
  

  useMountEffect(() => {
    // console.log("ran 1");
    const script = document.createElement("script");
    script.src = "//js-eu1.hsforms.net/forms/v2.js";
    script.async = true;
    document.body.appendChild(script);
    // console.log('res')

    script.addEventListener("load", () => {
      // console.log("ran 2");
      const HSform = document.getElementById("hubspotForm");

      if (window.hbspt && HSform.childElementCount !== 0) {
        console.log("Form Already Exists");
      } else {
        window.hbspt.forms.create({
          region: "eu1",
          portalId: "25935071",
          formId: "d218b8d2-7c06-4128-99e3-033b409881ee",
          target: "#hubspotForm"
        });
        // console.log("does not exist");
      }
    });
  }, []);

  return <div id="hubspotForm" className="px-2"></div>;
}

export default HubspotForm;
