import React from "react";
import "./Contact.module.css";
import { LinkednIcon, ContactRight, HubspotForm } from "../../components";
import { useQuery } from "@apollo/client";
import { GET_CONTENT } from "../../data";

function Contact() {
  const { loading, error, data } = useQuery(GET_CONTENT);
  if (loading) return <p></p>;
  if (error) return <p>Error :(</p>;
  return (
    <section className="bg-contactBG relative h-auto pb-8 pt-4 lg:h-150 items-center bg-center bg-cover">
      <div className="bg-almost-white h-auto w-card-90 relative lg:max-w-[1024px] m-auto pt-4 shadow-cardBox">
        <div className="px-2 lg:px-8 flex flex-col lg:flex-row w-full lg:space-x-8">
          <div className="w-full lg:w-55">
            <h2 className="font-medium my-4 pb-2 lg:p-2">Get in touch</h2>
            <HubspotForm
              region="eu1"
              portalId="25935071"
              formId="d218b8d2-7c06-4128-99e3-033b409881ee"
            />
          </div>
          <div className="w-full lg:w-45">
            <ContactRight />
          </div>
        </div>
        <div className="text-sm relative px-10 w-full">
          <div
            dangerouslySetInnerHTML={{
              __html: data.contactSection[0].smallText,
            }}
          ></div>
        </div>
        <div className="text-right text-almost-white">
          <LinkednIcon />
        </div>
      </div>
    </section>
  );
}

export default Contact;
