import React from "react";
import { CardContent } from "../../components";
import { illustration } from "../../resources/images";
import { useQuery } from "@apollo/client";
import { GET_CONTENT } from "../../data";

function Services() {
  const { loading, error, data } = useQuery(GET_CONTENT);

  
  if (loading) return <p></p>;
  if (error) return <p>Error :(</p>;
  return (
    <section
      id="services"
      className="bg-serviceBG relative xl:h-140 p-10 items-center bg-cover m-0"
    >
      <div className="pt-20 lg:pt-15 flex flex-col w-full max-w-card-80 space-y-4 xl:space-x-8 justify-around items-center xl:flex-row">
       
        { data.servicesSection.map((card) => {

          return ( 
          <div className="" key={card.id}>
            <CardContent
              title={card.title}
              img={illustration}
              btnText1={card.btnText1}
              btnText2={card.btnText2}
              btnText3={card.btnText3}
              footerContent={card.footerContent}
            />
          </div>
          )

        })
        }
      </div>
    </section>
  );
}

export default Services;
